"use client"
import { useEffect, useState } from "react"
import { supabase } from "../../../lib/supabaseClient"
import * as XLSX from "xlsx"
import BarangayBudgetBreakdown from "./components/barangayBudgetBreakdown" // Import the new component

export default function ReportsPage() {
  const [files, setFiles] = useState([])

  // ‚úÖ Fetch imported files
  useEffect(() => {
    const fetchFiles = async () => {
      const { data, error } = await supabase.storage.from("imports").list("", { limit: 100 })
      if (error) {
        console.error("Error fetching imports:", error)
      } else {
        const excelFiles = data.filter(f => f.name.endsWith(".xlsx") || f.name.endsWith(".csv"))
        const filesWithOriginalName = excelFiles.map(f => {
          const parts = f.name.split("_")
          const originalName = parts.slice(2).join("_")
          return { storageName: f.name, displayName: originalName }
        })

        setFiles(filesWithOriginalName)
      }
    }

    fetchFiles()
  }, [])

  return (
    <div className="p-6 space-y-8">
      <h1 className="text-2xl font-bold text-green-700">üìä Reports</h1>

      {/* =================== Import Files Section =================== */}
      <div className="bg-white p-6 rounded-lg shadow space-y-4">
        <h2 className="text-lg font-semibold text-gray-800">üìÇ Imported Files</h2>
        <div className="overflow-x-auto border rounded-lg">
          <ul>
            {files.map((file) => (
              <li key={file.storageName} className="py-2">
                <a href="#" className="text-blue-600">{file.displayName}</a>
              </li>
            ))}
          </ul>
        </div>
      </div>

      {/* =================== Barangay Budget Breakdown =================== */}
      <BarangayBudgetBreakdown selectedYear="all" selectedCategory="all" /> {/* Use the new component here */}
    </div>
  )
}
